* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  font-family: 'Rubik', sans-serif;
}
a{
  text-decoration: none;
  display: inline-block;
}
.form-control:focus{
  box-shadow: none;
  border-color: inherits;
}
.gap-5{
  gap:5px;
}
.gap-10{
  gap:10px;
}
.gap-15{
  gap:15px;
}
.gap-30{
  gap:30px;
}
.button{
  background-color: var(--color-232f3e);
  color: white;
  font-size: 14px;
  padding: 13px 33px;
  border-radius: 25px;
}
.button:hover{
  background-color: var(--color-febd69);
  color: #131921;
}
:root {
  --color-131921: #131921;
  --color-3b4149: #3b4149;
  --color-febd69: #febd69;
  --color-232f3e: #232f3e;
  --color-bf4800: #bf4800;
  --color-f5f5f7: #f5f5f7;
  --color-ededed: #ededed;
  --color-777777:#777777;
  --color-bf4800:#bf4800;
  --color-1c1c1b:#1c1c1b;
}

.card-wrapper{
  background-color: white;
  box-shadow: 0 0 10px #0000001a;
  padding: 15px;
}
.section-heading{
  font-size: 26px;
  font-weight: 500;
  line-height: 32px;
  margin-bottom: 30px;
}

/* --------- Header Part------------*/

.header-top-strip{
  background-color: var(--color-131921);
  border-bottom: 1px solid var(--color-3b4149);
}
.header-upper{
  background-color: var(--color-131921);
}
.header-upper .input-group-text{
  background-color: var(--color-febd69);
}
.header-upper .header-upper-links img{
  width: 30px;
  height: 30px;
}
.header-upper .header-upper-links .myfont{
   font-size: 14px;
}

.header-bottom{
  background-color: var(--color-232f3e);
}
.header-bottom .menu-links a,
.header-bottom .dropdown button{
  color: white;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  letter-spacing: .3;
  text-transform: uppercase;
}
.header-bottom .dropdown button:focus{
box-shadow: none;
}
.header-bottom .dropdown-menu{
  background-color: var(--color-131921);
  width: 100%;
  transform: translate3d(0px, 57px, 0px) !important;
}
.header-bottom .dropdown-item{
  padding:20px;
  margin-bottom: 3px;
  border-bottom: 1px solid var(--color-3b4149);
}
.header-bottom .dropdown-item:hover{
  background-color: transparent;
  color: var(--color-3b4149) !important;
}

/* --------- Header Part Ends Here------------*/

/*-----------Footer Parts Start----------------*/

footer{
  background-color: var(--color-232f3e);
}
footer:not(:first-child){
  border-top: 1px solid var(--color-3b4149);
}
footer .input-group-text{
  background-color: var(--color-232f3e);
  color: white;
  padding: 8px, 17px;
}
/*-----------Footer Parts Ends Here---------------*/

/* --------- Home Page------------*/

.main-banner-content{
  top:15%;
  left:5%;
}
.main-banner-content h4{
  color: var(--color-bf4800);
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  margin: 0 0 12px;
  letter-spacing: 0.3px;
  text-transform: uppercase;
}
.main-banner-content h5{
  padding: 0;
  font-size: 42px;
  font-weight: 500;
  line-height: 64px;
  letter-spacing: -2px;
  text-transform: none;
}
.main-banner-content p{
  font-size: 18px;
  line-height: 28px;
  letter-spacing: 0.4;
  color: var(--color-131921);
}

.small-banner-content{
  top:25%;
  left:10%;
}
.small-banner-content h4{
  color: var(--color-bf4800);
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
  margin: 0 0 12px;
  letter-spacing: 0.3px;
  text-transform: uppercase;
}
.small-banner-content h5{
  padding: 0;
  font-size: 22px;
  font-weight: 500;
  line-height: 34px;
  letter-spacing: -2px;
  text-transform: none;
}
.small-banner-content p{
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 0.4;
  color: var(--color-131921);
}
.small-banner{
  width: 49%;
}

/*----Home Wrapper 2-------*/
.home-wrapper-2{
  background-color: var(--color-f5f5f7);
}
/*----Home wrapper 2 Ends-------*/

/*  Categories section     */
.categories{
  background-color: white;
  box-shadow: 0 0 10px #0000001a;
  padding: 15px;
}
.categories>div:nth-child(1),
.categories>div:nth-child(2),
.categories>div:nth-child(3),
.categories>div:nth-child(4){
  border-bottom: 1px solid var(--color-ededed);
}
.categories>div:nth-child(1),
.categories>div:nth-child(2),
.categories>div:nth-child(3){
  border-right: 1px solid var(--color-ededed);
}
.categories>div:nth-child(5),
.categories>div:nth-child(6),
.categories>div:nth-child(7){
  border-right: 1px solid var(--color-ededed);
}
.categories > div{
  width: 24%;
  padding: 10px 10px 10px 20px;
}
.categories>div>div{
  width: 60%;
}
.categories .myImg{
  max-width: 55%;
  max-height: 100px;
}
/*   Categories Section Ends   */

/*-----------Home PAge Ends Heere----------------*/

/*-----------Blog Card --------------*/

.blog-card{
  background-color: white;
  padding: 20px;
  border-radius: 10px;

}
.blog-card img{
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
.blog-content{
  padding: 15px;
}
.blog-content .date{
  font-size: 13px;
  line-height: 24px;
  text-transform: uppercase;
  letter-spacing: 0;
  font-weight: 400;
  padding: 0;
  color: var(--color-777777);
}
.blog-content h5{
  font-size: 18px;
  color: var(--color-131921);
}
.blog-content p.desc{
  font-size: 13px;
  color: var(--color-777777);
}


.single-blog-card h3{
  font-size: 24px;
  font-weight: 600;
  color: var(--color-1c1c1b);
}
.single-blog-card p{
  color: var(--color-777777);
  font-size: 14px;
}
.single-blog-card a{
  font-size: 18px;
  color: var(--color-777777);
  margin-bottom: 20px;
}

/*-----------Blog Card Ends---------*/

/*-----------Product Card ---------*/

.product-card{
  padding: 15px;
  background-color: white;
  border-radius: 10px;
  overflow: hidden;
  width: 100%;
}
.product-card:hover .action-bar{
  right: 15px;
}
.product-card .product-image{
  height: 270px!important;
}
.product-card .product-image img{
  object-fit: contain;
  height: 270px;
}
     /*   Images changes on hover     */
.product-card .product-image > img:last-child{
  display: none;
}
.product-card:hover .product-image > img:nth-child(1){
  display: none;
}
.product-card:hover .product-image>img:last-child{
  display: block;
}
/*   Images changes on hover ends    */
.product-card .product-details h6{
  color: var(--color-bf4800);
  font-size: 13px;
}
.product-card .product-details h5{
  color: var(--color-1c1c1b);
  font-size: 15px;
}
.product-card .product-details p.price{
  color: var(--color-1c1c1b);
  font-size: 16px;
}
.product-card p.description{
  font-size: 13px;
  color: var(--color-777777);
  margin-right: 20px;
}
.action-bar{
  top: 10%;
  right: -23px;
  transition: 0.3s;
}
.wishlist-icon{
  top:2%;
  right: 15px;
}
.gr-12{
  width:100%;
}
.gr-6{
  width:49%;
}
.gr-4{
  width:32%;
}
.gr-3{
  width:24%;
}
.gr-12 .product-card {
  display: flex;
  gap: 20px;
}
/*-----------Product Card Ends---------*/

/*-----------Special Product Card ---------*/
.special-product-card{
  padding: 20px 10px;
  border-radius: 10px;
  background-color: white;
}
.special-product-card .badge{
  background-color: rgb(219, 56, 56) !important;
}
.special-product-card img{
  height: 300px;
  width: 100%;
}
/*----------- SpecialProduct Card Ends---------*/

/*--------------------Famous Card---------------*/
.famous-card{
  background-color: white;
  border-radius: 10px;
}
.famous-card .famous-content{
  top:8%;
  left:10%
}
.famous-card img{
  border-radius: 10px;
 height: 400px;
 width: 100%;
}
.famous-content h5{
  color: white;
  font-size: 13px;
  line-height: 20px;
  font-weight: 400px;
  margin-bottom: 7px;
  text-transform: uppercase;
}
.famous-content h6{
  font-size: 24px;
  line-height: 28px;
  font-weight: 500px;
  color: white;

}
.famous-content p{
  font-size: 16px;
  line-height: 24px;
  font-weight: 100;
  color: white;
}
/*--------------------Famous Card Ends Here---------------*/

/*========================Our Store Page===================*/

.filter-card{
  background-color: white;
  border-radius: 10px;
  padding: 15px 15px 25px;
}
.filter-title{
  font-size: 16px;
  line-height: 20px;
  font-weight: 600;
  color: var(--color-1c1c1b);
  margin-bottom: 20px;
}
.filter-card ul,
.filter-card .form-check-label{
  list-style: none;
  font-size: 13px;
  line-height: 30px;
  color: var(--color-777777);
  cursor: pointer;
}
.filter-card .form-check-input:checked{
  background-color: var(--color-febd69);
  border-color: var(--color-febd69);
}
.filter-card .form-check-input:focus{
  box-shadow: none;
  border-color: var(--color-febd69);
}
.sub-title{
  font-size: 14px;
  font-weight: 600;
  color: var(--color-1c1c1b);
  margin-bottom: 15px;
  margin-top: 20px;
}
.colors{
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}
.colors li{
  width: 20px;
  height: 20px;
  background-color: red;
  border-radius: 50%;

}
.random-products h5{
  font-size: 14px;
  margin-bottom: 8px;
}
.random-products:first-child{
  border-bottom: 1px solid var(--color-ededed);
}
.filter-sort-grid{
  padding: 10px;
  background-color: white;
  border-radius: 10px;
}
.grid img{
 width:34px;
 height: 34px;
 background-color: #f2f2f2;
 padding: 8px;
 border-radius: 3px;
}
/*========================Our Store PAge Ends Here===============*/

/*========================Contact Page===============*/

.contact-innerwrapper{
  padding: 30px 20px;
  border-radius: 15px;
  background-color: white;
  gap: 15px;
}
.contact-innerwrapper > div{
  width: 48%;
}
.contact-title{
  font-size: 26px;
  font-weight: 500;
  line-height: 35px;
  text-align: left;
 
}
.contact-innerwrapper form .form-control{
  background-color: var(--color-f5f5f7);
  border: none;
  padding: 20px;
}
.contact-innerwrapper ul{
  list-style: none;
}
.contact-innerwrapper ul li a,.contact-innerwrapper ul li{
  color: var(--color-777777);
}
/*========================Contact PAge Ends Here===============*/

/*========================Compare products ===============*/
.compare-product-card{
  padding: 20px 15px;
  background-color: white;
  border-radius: 10px;
}
.compare-product-card .cross{
  top:15px;
  right: 15px;
  width: 25px;
  padding: 3px;
  cursor: pointer;
}
.compare-product-card .details h5.title{
  font-size: 15px;
  line-height: 22px;
  font-weight: 500;
  color: var(--color-1c1c1b);
}
.compare-product-card .details h6.price{
  font-size: 15px;
  line-height: 22px;
  font-weight: 400;
}
.compare-product-card .product-detail{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0px;
  border-top: 2px solid var(--color-f5f5f7);
}
/*========================Compare Products PAge Ends Here===============*/

/*========================Wishlist==========================*/
.wishlist-card .cross{
  top:15px;
  right: 15px;
  width: 25px;
  padding: 3px;
  cursor: pointer;
}
.wishlist-card h5.title{
  font-size: 15px;
  line-height: 22px;
  font-weight: 500;
  color: var(--color-1c1c1b);
}
.wishlist-card h6.price{
  font-size: 15px;
  line-height: 22px;
  font-weight: 400;
}
.wishlist-card .wishlist-card-image img{
  width: 300px;
  height: 300px;
}
/*========================Wishlist ends Here=================*/

