/*=========================Policy Pages=====================*/

.policy{
  background-color: white;
  padding: 30px;
}
/*==========================Policy Pages Ends Here===========*/

/*==========================Single Product Page=================*/
.description-wrapper h4,
.reviews-wrapper h3{
font-size: 26px;
color: var(--color-1c1c1b);
margin-bottom: 10px;
}
.description-wrapper p{
  font-size: 14px;
  color: var(--color-777777);
}
.review-inner-wrapper{
  background-color: white;
  padding: 30px;
}
.review-head{
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  padding-bottom: 20px;
}
.review-head h4{
  font-size: 18px;
  color: var(--color-1c1c1b);

}
.review-head p,
.review-head a{
  font-size: 14px; 
  color: var(--color-777777);
}
.review-form h4{
  font-size: 16px;
  color: var(--color-777777);
}
.review p{
  font-size: 14px;
  color: var(--color-777777);
}
.review:not(:last-child){
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
.review-form{
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
.main-product-image{
  padding:20px;
  background-color: white;
  border-radius: 10px;
}
.main-product-image>div{
  border: 1px solid rgba(0, 0, 0, 0.1);
}
.main-product-image img{
  width: 90%!important;
  height: 500px !important;
  object-fit: cover;
}
.other-product-images{
  background-color: white;
  padding: 20px;
}
.other-product-images>div{
  border: 1px solid rgba(0, 0, 0, 0.1);
  padding: 20px;
  width: 48%;
}
.main-product-image .js-image-zoom__zoomed-image{
 left:60px !important;
 padding: 10px;
 background-color: white;
 border-radius: 10px;
 z-index: 1000;
}
.main-product-details{
  padding: 30px 30px;
  border-radius: 10px;
  background-color: white;
}
.border-bottom{
  border-bottom: 1px solid rgba(0, 0, 0, 0.25);
}
.main-product-details h3.title{
  font-size: 18px;
  font-weight: 600;
  color: var(--color-1c1c1b);
  margin-bottom: 10px;
}
.main-product-details p.price{
  font-size: 16px;
  line-height: 20px;
  font-weight: 600;
  margin-bottom: 10px;
}
.main-product-details a.review-btn,
.t-review{
  color: var(--color-777777);
  font-size: 13px;
}
.product-heading{
  font-size: 14px;
  color: var(--color-1c1c1b);
  margin-bottom: 0px;
}
.product-data{
  font-size: 13px;
  color: var(--color-777777);
  margin-bottom: 0px;
}
.main-product-details a{
  font-size: 14px;
  color: var(--color-1c1c1b);
}
/*==========================Single Product Pages Ends Here===========*/

/*==========================Cart Pages ===========*/
.cart-header,
.cart-data{
  border-bottom: 1px solid #eaeaea;
}
.cart-header h4{
  font-size: 23px;
  color: var(--color-777777);
  text-transform: uppercase;
}
.cart-col-1{
  width: 40%;
}
.cart-col-1 p{
  color: var(--color-777777);
  font-size: 16px;
}
.cart-col-2{
  width: 10%;
}
.cart-col-3{
  width: 15%;
}
.delete_icon{
  border-radius: 50%;
  color: white;
  padding: 8px 12px;
  cursor: pointer;
}
.delete_icon:hover{
  transform: scale(1.2);
}
.cart-col-4{
  width: 15%;
}
.cart-data h5 .price{
  font-size: 14px;
  font-weight: bold;
  color: var(--color-1c1c1b);
}
/*==========================Cart Page Pages Ends Here===========*/

/*==========================Checkout Pages ===========*/
.border-bottom .badge{
  padding: 8px 12px;
  top: -12px;
  right: -5px;
}
.total{
  font-size: 16px;
  color: var(--color-1c1c1b);
}
.total-price{
  font-size: 16px;
  color: var(--color-777777);
}
/*==========================Checkout Pages Ends Here===========*/

/*==========================Shipping Pages ===========*/
.ship-method{
  border: 1px solid var(--color-777777);
  border-radius: 5px;
  padding: 15px;
}
.ship-method .title{
  font-size: 16px;
  color: var(--color-777777);
  padding: 0px;
  margin: 0;
}
.ship-method .total{
  font-size: 16px;
  color: var(--color-777777);
  padding: 0px;
  margin: 0;
}
/*==========================Shipping Pages Ends Here===========*/